<template>
    <div class="myLogin">
        <div class="myLogin-title">
            <h1 class="icon-logo" style="position:relative;padding-left:3.5rem;box-sizing:border-box"></h1>
            <h1 class="login-txt l_orangeColor"></h1>
        </div>
        <div class="myLogin-section">
            <ul>
                <li class="inputTxt">
                    <input id="userName" type="text" placeholder="/" class="l_orangeColor" maxlength="20" v-model="userName"  @focus="handleFocus"/>
                    <p class="l_close l_gay9">+</p>
                    <h5 class="l_errorTip l_orangeColor"></h5>
                </li>
                <li class="inputUpwd">
                    <input id="passWord" type="password" placeholder="8-20+" class="l_orangeColor" maxlength="20" v-model="passWord"  @focus="handleFocus"/>
                     <p class="l_pswBtn  l_gayColor"></p>
                    <h5 class="l_errorTip l_orangeColor"></h5>
                </li>
                <li class="forgetPwd"><a href="#" class="l_gay9"> ?</a></li>
            </ul>
            <button class="l_btn" @click="doLogin"></button>
            <router-link to="register"  class="register l_gay9"> ></router-link>
        </div>
    </div>
</template>
<style scoped>
    /*base.css*/
    .myLogin{
        width:100%;
        height:auto;
    }
    /*-logo*/
    .myLogin-title{
        width:90%;
        height:9rem;
        padding:5rem 5% 0;
    }
    .myLogin-title h1{
        width:100%;
        font-size:2.42rem;
        height:4rem;
        line-height:4rem;
        font-weight:400;
    }
    .icon-logo:before{
        content:"";
        position:absolute;
        top:0;
        left:-.6rem;
        width:4rem;
        height:4rem;
        background:url("../assets/images/login/icon_logo_orange.png") no-repeat;
        background-size:contain;
    }
    /**/
    button,
    input{
        outline:none;
        border-radius:0;
    }
    /*inputplaceholder*/
    /* ::-weblit-input-placeholder{
        color:#f00;
    } */
    input::-webkit-input-placeholder{
        color:#eee;
    }
    .myLogin-section{
        width:90%;
        padding:2rem 5%;
        height:auto;
    }
    .myLogin-section ul{
        width:100%;
        height:auto;
    }
    .myLogin-section ul li.inputTxt,
    .myLogin-section ul li.inputUpwd{
        width:100%;
        height:3rem;
        line-height:3rem;
        margin-bottom:2rem;
        border-bottom:2px solid #f1f1f1;
        position:relative;
    }
    .myLogin-section ul li input{
        width:80%;
        height:1.6rem;
        line-height:1.6rem;
        padding:.7rem 0;
        border:0;
        font-size:1.6rem;
        letter-spacing:.2.8rem;
    }
    /**/
    .l_close,
    .l_pswBtn{
        position:absolute;
        display:none;
    }
    .l_close{
        width:3rem;
        height:3rem;
        line-height:3rem;
        text-align:center;
        font-size:2rem;
        top:0;
        right:3%;
        transform:rotate(45deg);
    }
    /**/
    .l_pswBtn{
        width:2rem;
        height:2rem;
        right:5%;
        top:.5rem;
        background:url("../assets/images/login/register_icon01.png") 100% no-repeat;
        background-size:4rem;
    }
    .l_pswBtn1{
        background:url("../assets/images/login/register_icon01.png") 0 no-repeat;
        background-size:4rem;
    }
    /*h5*/
    .l_errorTip{
        position:absolute;
        width:100%;
        height:2rem;
        line-height:2rem;
        top:3rem;
        left:0;
        display:none;
    }
    /**/
    .forgetPwd{
        float:right;
        clear:both;
    }
    .l_btn{
        border:none;
        width:90%;
        height:4rem;
        border-radius:4rem;
        background:linear-gradient(to bottom,#ff5e46,#fea244);
        margin:4rem 5% 2rem;
        font-size:1.25rem;
        color:#fff;
        box-shadow:0 .2rem .4rem .1rem rgba(254,155,97,.6);
    }
    .register{
        display:block;
        width:8rem;
        height:2rem;
        line-height:2rem;
        margin:1rem auto 0;
        text-align:center;
    }
  
</style>
<script>
    import axios from "axios"
    export default {
        data: function () {
            return {
               userName:"",
               passWord:""
            }
        },
        components: {
        },
        methods: {
            doLogin:function(){
                if(this.userName==""){
                    $("#userName").siblings(".l_errorTip").show();
                }else if(this.passWord==""){
                    $("#passWord").siblings(".l_errorTip").show();
                }else {
                    //
                    //token
                    //yoghurt
                    global.sessionStorage.setItem("token",1);
                    // console.log()
                    this.$router.push("index")
                }
            },
            handleFocus:function(){
                 $("#userName").siblings(".l_errorTip").hide();
                  $("#passWord").siblings(".l_errorTip").hide();
            }
        },
        computed: {},
        watch: {},
        mounted: function () {
            $(function(){
                //
                $(".myLogin-section").on("focus","input",function(){
                   $(this).next().show();
                   //
                }).on("blur","input",function(){
                   $(this).next().hide();
                }).on("touchend",".l_pswBtn",function(e){
                    //
                    e.preventDefault();
                    $(this).toggleClass("l_pswBtn1")
                    if($(this).hasClass("l_pswBtn1")){
                         $(this).prev().attr("type","text")
                    }else{
                        $(this).prev().attr("type","password")
                    }   
                    // 
                }).on("touchend",".l_close",function(e){
                    e.preventDefault();
                    $(this).prev("#userName").val("");
                }).on("input","#userName",function(){
                    //2020
                    var reg=/^[a-zA-Z0-9]{6,20}$/;
                    var t=$(this);
                    if(!reg.test(t.val())){
                        if(t.val().length>=20){ 
                            t.val(t.val().slice(0,20))
                        }
                    }
                })
            })
        }
    }

</script>